.advantages {
  width: 100%;
  background-image: url("../img/decoration/bg-advantages-desktop.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom;
  margin-bottom: 103px;

  @media (min-resolution: 2dppx) and (min-width: $desktop-width) {
    background-image: url("../img/decoration/bg-advantages-desktop@2x.jpg");
  }

  @media (max-width: $desktop-width) {
    margin-bottom: 90px;
    background-image: url("../img/decoration/bg-advantages-tablet.jpg");

    @media (min-resolution: 2dppx) and (max-width: $desktop-width) {
      background-image: url("../img/decoration/bg-advantages-tablet@2x.jpg");
    }
  }

  @media (max-width: $tablet-width) {
    margin-bottom: 57px;
    background-image: url("../img/decoration/bg-advantages-mobile.jpg");

    @media (min-resolution: 2dppx) and (max-width: $tablet-width) {
      background-image: url("../img/decoration/bg-advantages-mobile@2x.jpg");
    }
  }
}

.advantages__wrapper {
  width: 1160px;
  margin: 0 auto;
  padding: 103px 0 127px;

  @media (max-width: $desktop-width) {
    padding: 90px 0 100px;
    width: 678px;
  }

  @media (max-width: $tablet-width) {
    padding: 57px 0 64px;
    width: 290px;
  }
}

.advantages__header {
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  text-align: center;
  margin: 0 0 49px;

  @media (max-width: $desktop-width) {
    font-size: 26px;
    line-height: 39px;
    margin-left: 7px;
  }

  @media (max-width: $tablet-width) {
    font-size: 24px;
    line-height: 36px;
    margin-bottom: 30px;
    margin-left: 0;
  }
}

.advantages__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(3, 360px);
  gap: 40px;

  @media (max-width: $desktop-width) {
    grid-template-columns: repeat(3, 206px);
    margin-left: 4px;
    gap: 30px;
  }

  @media (max-width: $tablet-width) {
    display: flex;
    flex-direction: column;
    margin-left: 0;
  }
}

.advantages__item {
  position: relative;
  width: 360px;
  min-height: 241px;
  padding: 81px 0 30px 30px;
  background-color: $title-white;
  box-sizing: border-box;

  &::before {
    content: "";
    position: absolute;
    display: block;
    top: 30px;
    left: 30px;
    background-repeat: no-repeat;
  }

  &--ship {
    .advantages__text {
      width: 290px;
    }

    &::before {
      width: 27px;
      height: 32px;
      background-image: url("../img/svg/advantages-ship.svg");
    }
  }

  &--all-inclusive {
    .advantages__text {
      &:first-of-type {
        width: 210px;
      }

      &:last-of-type {
        width: 290px;
      }
    }

    &::before {
      top: 35px;
      width: 38px;
      height: 21px;
      background-image: url("../img/svg/advantages-all-inclusive.svg");
    }
  }

  &--zodiac {
    .advantages__text {
      width: 241px;
    }

    &::before {
      top: 38px;
      width: 42px;
      height: 15px;
      background-image: url("../img/svg/advantages-zodiac.svg");
    }
  }

  @media (max-width: $desktop-width) {
    width: 206px;
    min-height: 310px;
    padding: 70px 0 15px 20px;

    &::before {
      left: 20px;
    }

    &--ship {
      .advantages__text {
        width: 154px;
      }

      &::before {
        width: 21px;
        height: 26px;
        top: 26px;
      }
    }

    &--all-inclusive {
      .advantages__text {
        &:first-of-type,
        &:last-of-type {
          width: 162px;
        }
      }

      &::before {
        width: 33px;
        height: 19px;
        top: 30px;
      }
    }

    &--zodiac {
      .advantages__text {
        width: 167px;
      }

      &::before {
        width: 38px;
        height: 13px;
        top: 32px;
      }
    }
  }

  @media (max-width: $tablet-width) {
    width: 290px;
    min-height: 250px;
    padding: 70px 15px 15px 20px;

    .advantages__text {
      width: auto;
    }

    &--all-inclusive {
      .advantages__text {
        &:first-of-type {
          width: 210px;
        }

        &:last-of-type {
          width: 249px;
        }
      }

      &::before {
        top: 29px;
      }
    }

    &--zodiac {
      .advantages__title {
        width: 151px;
      }

      .advantages__text {
        width: 233px;
      }

      &::before {
        top: 31px;
      }
    }
  }
}

.advantages__title {
  margin: 0 0 10px 0;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;

  @media (max-width: $desktop-width) {
    margin-bottom: 6px;
  }
}

.advantages__text {
  line-height: 18px;
  margin: 0;

  @media (max-width: $desktop-width) {
    line-height: 20px;
  }
}

.advantages__break {
  display: none;

  &--desktop {
    display: inline;
  }

  @media (max-width: $desktop-width) {
    &--desktop {
      display: none;
    }

    &--tablet {
      display: inline;
    }
  }

  @media (max-width: $tablet-width) {
    &--tablet {
      display: none;
    }

    &--mobile {
      display: inline;
    }
  }
}
